import React, { useState, useMemo } from 'react';
import Table from '../../UI/Table/Table';
import Badge from '../../UI/Badge/Badge';
import Card from '../../UI/Card/Card';
import Button from '../../UI/Button/Button';
import ConfirmModal from '../../UI/Modal/ConfirmModal';
import { useNotification } from '../../../context/NotificationContext';
import './SystemLogs.scss';

const SystemLogs = ({ logs }) => {
  const { showSuccess } = useNotification();
  const [filter, setFilter] = useState('all');
  const [dateRange, setDateRange] = useState('all');
  const [searchTerm, setSearchTerm] = useState('');
  const [showClearConfirm, setShowClearConfirm] = useState(false);

  const filteredLogs = useMemo(() => {
    let filtered = [...logs];

    if (filter !== 'all') {
      filtered = filtered.filter(log => {
        if (filter === 'login') return log.action.includes('login');
        if (filter === 'create') return log.action.includes('create');
        if (filter === 'submit') return log.action.includes('submit');
        if (filter === 'grade') return log.action.includes('grade');
        return true;
      });
    }

    if (dateRange !== 'all') {
      const now = new Date();
      const filterDate = new Date();
      
      switch (dateRange) {
        case 'today':
          filterDate.setHours(0, 0, 0, 0);
          break;
        case 'week':
          filterDate.setDate(now.getDate() - 7);
          break;
        case 'month':
          filterDate.setMonth(now.getMonth() - 1);
          break;
        default:
          break;
      }

      filtered = filtered.filter(log => new Date(log.timestamp) >= filterDate);
    }

    if (searchTerm.trim()) {
      const term = searchTerm.toLowerCase();
      filtered = filtered.filter(log => 
        log.user.toLowerCase().includes(term) ||
        log.action.toLowerCase().includes(term) ||
        log.details.toLowerCase().includes(term)
      );
    }

    return filtered;
  }, [logs, filter, dateRange, searchTerm]);

  const getActionVariant = (action) => {
    if (action.includes('login')) return 'info';
    if (action.includes('create')) return 'success';
    if (action.includes('submit')) return 'warning';
    if (action.includes('grade')) return 'primary';
    return 'default';
  };

  const columns = [
    {
      key: 'timestamp',
      title: '',
      width: '15%',
      render: (value) => new Date(value).toLocaleString('ru-RU')
    },
    {
      key: 'user',
      title: '',
      width: '15%'
    },
    {
      key: 'action',
      title: '',
      width: '20%',
      render: (value) => {
        const actionLabels = {
          login: '  ',
          logout: '  ',
          create_assignment: ' ',
          submit_work: ' ',
          grade_submission: ' ',
          create_course: ' ',
          update_user: ' '
        };
        
        const label = actionLabels[value] || value;
        return (
          <Badge variant={getActionVariant(value)}>
            {label}
          </Badge>
        );
      }
    },
    {
      key: 'details',
      title: '',
      width: '45%',
      render: (value) => (
        <span className="log-details">{value}</span>
      )
    },
    {
      key: 'ip',
      title: 'IP',
      width: '10%',
      render: (value) => value || 'N/A'
    }
  ];

  const handleExportLogs = () => {
    const csvContent = [
      ['', '', '', '', 'IP'],
      ...filteredLogs.map(log => [
        new Date(log.timestamp).toLocaleString('ru-RU'),
        log.user,
        log.action,
        log.details,
        log.ip || 'N/A'
      ])
    ].map(row => row.map(cell => `"${String(cell ?? '').replace(/"/g, '""')}"`).join(',')).join('\n');

    const BOM = '\uFEFF';
    const blob = new Blob([BOM + csvContent], { type: 'text/csv;charset=utf-8;' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `system-logs-${new Date().toISOString().split('T')[0]}.csv`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const handleClearLogs = () => {
    setShowClearConfirm(true);
  };

  const confirmClearLogs = () => {
    showSuccess('  ( -   )');
    setShowClearConfirm(false);
  };

  return (
    <div className="system-logs">
      <div className="section-header">
        <div className="header-left">
          <h2> </h2>
          <p className="log-count">
             {filteredLogs.length}  {logs.length} 
          </p>
        </div>
        <div className="header-actions">
          <Button variant="secondary" size="small" onClick={handleExportLogs}>
              CSV
          </Button>
          <Button variant="danger" size="small" onClick={handleClearLogs}>
              
          </Button>
        </div>
      </div>

      <Card className="logs-filters">
        <div className="filters-grid">
          <div className="filter-group">
            <label> :</label>
            <select 
              value={filter} 
              onChange={(e) => setFilter(e.target.value)}
              className="filter-select"
            >
              <option value="all"> </option>
              <option value="login">/</option>
              <option value="create"></option>
              <option value="submit"> </option>
              <option value="grade"></option>
            </select>
          </div>

          <div className="filter-group">
            <label>:</label>
            <select 
              value={dateRange} 
              onChange={(e) => setDateRange(e.target.value)}
              className="filter-select"
            >
              <option value="all">  </option>
              <option value="today"></option>
              <option value="week"> </option>
              <option value="month"> </option>
            </select>
          </div>

          <div className="filter-group">
            <label>:</label>
            <input
              type="text"
              placeholder="  ..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="search-input"
            />
          </div>
        </div>
      </Card>

      <Card className="logs-table-container">
        {filteredLogs.length === 0 ? (
          <div className="empty-logs">
            <div className="empty-icon"></div>
            <h3>  </h3>
            <p>   </p>
          </div>
        ) : (
          <Table
            columns={columns}
            data={filteredLogs}
            striped
            hoverable
            className="logs-table"
          />
        )}
      </Card>

      <div className="logs-summary">
        <div className="summary-card">
          <h4> </h4>
          <div className="summary-stats">
            <div className="summary-stat">
              <span className="stat-label"> :</span>
              <span className="stat-value">{logs.length}</span>
            </div>
            <div className="summary-stat">
              <span className="stat-label">:</span>
              <span className="stat-value">{filteredLogs.length}</span>
            </div>
            <div className="summary-stat">
              <span className="stat-label"> :</span>
              <span className="stat-value">
                {(() => {
                  const userCounts = {};
                  logs.forEach(log => {
                    userCounts[log.user] = (userCounts[log.user] || 0) + 1;
                  });
                  const mostActive = Object.entries(userCounts).sort((a, b) => b[1] - a[1])[0];
                  return mostActive ? `${mostActive[0]} (${mostActive[1]})` : ' ';
                })()}
              </span>
            </div>
          </div>
        </div>
      </div>

      <ConfirmModal
        isOpen={showClearConfirm}
        onClose={() => setShowClearConfirm(false)}
        onConfirm={confirmClearLogs}
        title=" "
        message=" ,     ?    ."
        confirmText=""
        cancelText=""
        danger={true}
      />
    </div>
  );
};

export default SystemLogs;