export const validateLoginForm = (formData) => {
  const errors = {};

  const trimmedLogin = formData.login?.trim() || '';
  if (!trimmedLogin) {
    errors.login = '  email ';
  }

  if (!formData.role) {
    errors.role = '  ';
  }

  if (!formData.password) {
    errors.password = ' ';
  } else if (formData.password.length < 6) {
    errors.password = '    6 ';
  }

  return {
    isValid: Object.keys(errors).length === 0,
    errors
  };
};

export const validateRegisterForm = (formData) => {
  const errors = {};

  const trimmedName = formData.name?.trim() || '';
  if (!trimmedName) {
    errors.name = ' ';
  } else if (trimmedName.length < 2) {
    errors.name = '    2 ';
  } else if (trimmedName.length > 100) {
    errors.name = '    100 ';
  }

  const trimmedLogin = formData.login?.trim() || '';
  if (!trimmedLogin) {
    errors.login = ' ';
  } else if (trimmedLogin.length < 3) {
    errors.login = '    3 ';
  } else if (trimmedLogin.length > 30) {
    errors.login = '    30 ';
  } else if (!/^[a-zA-Z0-9_]+$/.test(trimmedLogin)) {
    errors.login = '     ,   ';
  }

  const trimmedEmail = formData.email?.trim() || '';
  if (!trimmedEmail) {
    errors.email = 'Email ';
  } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(trimmedEmail)) {
    errors.email = '  email ';
  } else if (trimmedEmail.length > 255) {
    errors.email = 'Email    255 ';
  }

  if (!formData.password) {
    errors.password = ' ';
  } else if (formData.password.length < 8) {
    errors.password = '    8 ';
  } else if (formData.password.length > 128) {
    errors.password = '    128 ';
  } else if (!/(?=.*[a-z])/.test(formData.password)) {
    errors.password = '       ';
  } else if (!/(?=.*[A-Z])/.test(formData.password)) {
    errors.password = '       ';
  } else if (!/(?=.*\d)/.test(formData.password)) {
    errors.password = '      ';
  }

  if (!formData.confirmPassword) {
    errors.confirmPassword = '  ';
  } else if (formData.password !== formData.confirmPassword) {
    errors.confirmPassword = '  ';
  }

  const trimmedGroup = formData.group?.trim() || '';
  if (formData.role === 'student') {
    if (!trimmedGroup) {
      errors.group = '   ';
    } else if (!/^[-A-Z\-\d]+$/i.test(trimmedGroup)) {
      errors.group = '    ,    (, -401)';
    } else if (trimmedGroup.length > 20) {
      errors.group = '    20 ';
    }
  }

  return {
    isValid: Object.keys(errors).length === 0,
    errors
  };
};

export const validateAssignmentForm = (formData) => {
  const errors = {};

  const trimmedTitle = formData.title?.trim() || '';
  if (!trimmedTitle) {
    errors.title = '  ';
  } else if (trimmedTitle.length < 3) {
    errors.title = '    3 ';
  } else if (trimmedTitle.length > 200) {
    errors.title = '    200 ';
  }

  const trimmedCourse = formData.course?.trim() || '';
  if (!trimmedCourse) {
    errors.course = '  ';
  } else if (trimmedCourse.length > 100) {
    errors.course = '     100 ';
  }

  const trimmedDescription = formData.description?.trim() || '';
  if (!trimmedDescription) {
    errors.description = '  ';
  } else if (trimmedDescription.length < 10) {
    errors.description = '    10 ';
  } else if (trimmedDescription.length > 5000) {
    errors.description = '    5000 ';
  }

  if (!formData.deadline) {
    errors.deadline = '  ';
  } else {
    const deadline = new Date(formData.deadline);
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    
    if (deadline < today) {
      errors.deadline = '      ';
    }
    
    const maxDate = new Date();
    maxDate.setFullYear(maxDate.getFullYear() + 2);
    if (deadline > maxDate) {
      errors.deadline = '        2 ';
    }
  }

  const maxScore = Number(formData.maxScore);
  if (!formData.maxScore || isNaN(maxScore) || maxScore <= 0) {
    errors.maxScore = '     ';
  } else if (maxScore > 1000) {
    errors.maxScore = '     1000';
  } else if (!Number.isInteger(maxScore)) {
    errors.maxScore = '     ';
  }

  if (!formData.studentGroups || !Array.isArray(formData.studentGroups) || formData.studentGroups.length === 0) {
    errors.studentGroups = '    ';
  }

  return {
    isValid: Object.keys(errors).length === 0,
    errors
  };
};

export const validateProfileForm = (formData) => {
  const errors = {};

  const trimmedName = formData.name?.trim() || '';
  if (!trimmedName) {
    errors.name = ' ';
  } else if (trimmedName.length < 2) {
    errors.name = '    2 ';
  } else if (trimmedName.length > 100) {
    errors.name = '    100 ';
  }

  const trimmedEmail = formData.email?.trim() || '';
  if (!trimmedEmail) {
    errors.email = 'Email ';
  } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(trimmedEmail)) {
    errors.email = '  email ';
  } else if (trimmedEmail.length > 255) {
    errors.email = 'Email    255 ';
  }

  const trimmedPhone = formData.phone?.trim() || '';
  if (trimmedPhone) {
    const phoneDigits = trimmedPhone.replace(/\D/g, '');
    if (!/^(\+?7|8)?[0-9]{10}$/.test(phoneDigits) || phoneDigits.length < 10) {
      errors.phone = '    (, +7 999 123-45-67  8 999 123-45-67)';
    }
  }

  const trimmedGroup = formData.group?.trim() || '';
  if (trimmedGroup && !/^[-A-Z\-\d]+$/i.test(trimmedGroup)) {
    errors.group = '    ,    (, -401)';
  } else if (trimmedGroup && trimmedGroup.length > 20) {
    errors.group = '    20 ';
  }

  const trimmedDepartment = formData.department?.trim() || '';
  if (trimmedDepartment && trimmedDepartment.length > 100) {
    errors.department = '    100 ';
  }

  const trimmedBio = formData.bio?.trim() || '';
  if (trimmedBio && trimmedBio.length > 500) {
    errors.bio = '    500 ';
  }

  return {
    isValid: Object.keys(errors).length === 0,
    errors
  };
};

export const validatePasswordChange = (formData) => {
  const errors = {};

  if (!formData.currentPassword) {
    errors.currentPassword = '  ';
  }

  if (!formData.newPassword) {
    errors.newPassword = '  ';
  } else if (formData.newPassword.length < 8) {
    errors.newPassword = '    8 ';
  } else if (!/(?=.*[a-z])/.test(formData.newPassword)) {
    errors.newPassword = '       ';
  } else if (!/(?=.*[A-Z])/.test(formData.newPassword)) {
    errors.newPassword = '       ';
  } else if (!/(?=.*\d)/.test(formData.newPassword)) {
    errors.newPassword = '      ';
  }

  if (!formData.confirmPassword) {
    errors.confirmPassword = '  ';
  } else if (formData.newPassword !== formData.confirmPassword) {
    errors.confirmPassword = '  ';
  }

  return {
    isValid: Object.keys(errors).length === 0,
    errors
  };
};