import React from 'react';
import './Table.scss';

const Table = ({ 
  columns, 
  data, 
  className = '',
  striped = false,
  hoverable = false,
  emptyText = '  ',
  ...props 
}) => {
  if (!data || data.length === 0) {
    return (
      <div className={`table-empty ${className}`}>
        <div className="table-empty__content">
          <div className="table-empty__icon"></div>
          <div className="table-empty__text">{emptyText}</div>
        </div>
      </div>
    );
  }

  return (
    <div className={`table-container ${className}`}>
      <table 
        className={`table ${striped ? 'table--striped' : ''} ${hoverable ? 'table--hoverable' : ''}`} 
        {...props}
      >
        <thead>
          <tr>
            {columns.map((column, index) => (
              <th 
                key={index} 
                style={{ width: column.width }}
                className={column.className}
              >
                {column.title}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {data.map((row, rowIndex) => {
            const rowKey = row.id ?? row.key ?? `row-${rowIndex}`;
            return (
              <tr key={rowKey}>
                {columns.map((column, colIndex) => {
                  const cellKey = `${rowKey}-${column.key || colIndex}`;
                  return (
                    <td key={cellKey} className={column.cellClassName}>
                      {column.render ? column.render(row[column.key], row, rowIndex) : row[column.key]}
                    </td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};

export default Table;