import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import logo from '../../assets/logo.svg';
import './Welcome.scss';

const Welcome = () => {
  const navigate = useNavigate();
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    setIsVisible(true);
  }, []);

  const handleGetStarted = () => {
    navigate('/login');
  };

  const handleRoleNavigation = (role) => {
    navigate('/login', { state: { preselectedRole: role } });
  };

  const features = [
    {
      icon: '',
      title: ' ',
      description:
        '  ,   ,     ',
      gradient: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    },
    {
      icon: '',
      title: '  ',
      description:
        '     ,      ',
      gradient: 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)',
    },
    {
      icon: '',
      title: ' ',
      description:
        '  ,         ',
      gradient: 'linear-gradient(135deg, #4facfe 0%, #00f2fe 100%)',
    },
    {
      icon: '',
      title: '  ',
      description:
        '   ,      ',
      gradient: 'linear-gradient(135deg, #43e97b 0%, #38f9d7 100%)',
    },
  ];

  const roles = [
    {
      icon: '',
      title: '',
      features: [
        '     ',
        '    ',
        '    ',
        '     ',
      ],
      gradient: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      role: 'student',
    },
    {
      icon: '',
      title: '',
      features: [
        '    ',
        '     ',
        '   ',
        '   ',
      ],
      gradient: 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)',
      role: 'teacher',
    },
    {
      icon: '',
      title: '',
      features: [
        '    ',
        '   ',
        '    ',
        '  ',
      ],
      gradient: 'linear-gradient(135deg, #4facfe 0%, #00f2fe 100%)',
      role: 'admin',
    },
  ];

  return (
    <div className={`welcome-page ${isVisible ? 'visible' : ''}`}>
      <div className='animated-background'>
        <div className='shape shape-1'></div>
        <div className='shape shape-2'></div>
        <div className='shape shape-3'></div>
      </div>

      <nav className='welcome-nav'>
        <div className='nav-content'>
          <Link to='/welcome' className='nav-brand'>
            <div className='logo'>
              <img src={logo} alt='' />
            </div>
          </Link>
          <button className='nav-login-btn' onClick={handleGetStarted}>
              
          </button>
        </div>
      </nav>

      <section className='hero-section'>
        <div className='hero-background'>
          <div className='hero-orbit orbit-1'></div>
          <div className='hero-orbit orbit-2'></div>
          <div className='hero-orbit orbit-3'></div>
        </div>

        <div className='hero-content'>
          <div className='hero-badge'>
            <span className='badge-icon'></span>
              
          </div>

          <h1 className='hero-title'>
            <span className='title-line'> </span>
            <span className='title-line'>
               <span className='title-accent'> </span>
            </span>
          </h1>

          <p className='hero-description'>
                 .
             ,     
                .
          </p>

          <div className='hero-actions'>
            <button
              className='btn-primary btn-large btn-glow'
              onClick={handleGetStarted}
            >
              <span className='btn-text'> </span>
              <span className='btn-icon'></span>
            </button>
            <button
              className='btn-outline btn-large'
              onClick={() =>
                document
                  .getElementById('features')
                  .scrollIntoView({ behavior: 'smooth' })
              }
            >
               
            </button>
          </div>

          <div className='hero-stats'>
            <div className='stat-item'>
              <div className='stat-number'>1000+</div>
              <div className='stat-label'></div>
            </div>
            <div className='stat-item'>
              <div className='stat-number'>50+</div>
              <div className='stat-label'></div>
            </div>
            <div className='stat-item'>
              <div className='stat-number'>5000+</div>
              <div className='stat-label'> </div>
            </div>
          </div>
        </div>

        <div className='hero-visual'>
          <div className='floating-cards'>
            <div className='floating-card card-1'>
              <div className='card-icon'></div>
              <div className='card-title'></div>
              <div className='card-subtitle'> </div>
            </div>
            <div className='floating-card card-2'>
              <div className='card-icon'></div>
              <div className='card-title'></div>
              <div className='card-subtitle'> </div>
            </div>
            <div className='floating-card card-3'>
              <div className='card-icon'></div>
              <div className='card-title'></div>
              <div className='card-subtitle'> </div>
            </div>
          </div>
        </div>
      </section>

      <section id='features' className='features-section'>
        <div className='container'>
          <div className='section-header'>
            <h2 className='section-title'> </h2>
            <p className='section-subtitle'>
                     
               
            </p>
          </div>

          <div className='features-grid'>
            {features.map((feature, index) => (
              <div
                key={index}
                className='feature-card'
                style={{ '--gradient': feature.gradient }}
              >
                <div className='feature-icon-wrapper'>
                  <div className='feature-icon-bg'></div>
                  <span className='feature-icon'>{feature.icon}</span>
                </div>
                <h3 className='feature-title'>{feature.title}</h3>
                <p className='feature-description'>{feature.description}</p>
                <div className='feature-decoration'></div>
              </div>
            ))}
          </div>
        </div>
      </section>

      <section className='roles-section'>
        <div className='container'>
          <div className='section-header'>
            <h2 className='section-title'>  </h2>
            <p className='section-subtitle'>
                    
               
            </p>
          </div>

          <div className='roles-grid'>
            {roles.map((role, index) => (
              <div
                key={index}
                className='role-card'
                onClick={() => handleRoleNavigation(role.role)}
                style={{ '--gradient': role.gradient }}
              >
                <div className='role-header'>
                  <div className='role-icon-wrapper'>
                    <span className='role-icon'>{role.icon}</span>
                  </div>
                  <h3 className='role-title'>{role.title}</h3>
                </div>

                <ul className='role-features'>
                  {role.features.map((feature, featureIndex) => (
                    <li key={featureIndex} className='role-feature'>
                      {feature}
                    </li>
                  ))}
                </ul>

                <button className='role-action'>
                  <span>  {role.title.toLowerCase()}</span>
                  <span className='action-arrow'></span>
                </button>

                <div className='role-decoration'></div>
              </div>
            ))}
          </div>
        </div>
      </section>

      <section className='cta-section'>
        <div className='container'>
          <div className='cta-content'>
            <h2 className='cta-title'>  ?</h2>
            <p className='cta-description'>
                 ,   
                 
            </p>
            <button
              className='btn-primary btn-xlarge btn-glow'
              onClick={handleGetStarted}
            >
              <span className='btn-text'>  </span>
              <span className='btn-icon'></span>
            </button>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Welcome;
