import React, { useState } from 'react';
import Table from '../../UI/Table/Table';
import Button from '../../UI/Button/Button';
import Badge from '../../UI/Badge/Badge';
import Card from '../../UI/Card/Card';
import ConfirmModal from '../../UI/Modal/ConfirmModal';
import './CourseManagement.scss';

const CourseManagement = ({ 
  courses = [], 
  teachers = [], 
  onCreateCourse, 
  onUpdateCourse,
  onDeleteCourse,
  loading = false,
  className = ""
}) => {
  const [showForm, setShowForm] = useState(false);
  const [editingCourse, setEditingCourse] = useState(null);
  const [formData, setFormData] = useState({
    name: '',
    code: '',
    teacherId: '',
    description: '',
    semester: 1,
    status: 'active'
  });
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [courseToDelete, setCourseToDelete] = useState(null);
  const [formErrors, setFormErrors] = useState({});

  const handleCreate = () => {
    setFormData({
      name: '',
      code: '',
      teacherId: '',
      description: '',
      semester: 1,
      status: 'active'
    });
    setEditingCourse(null);
    setFormErrors({});
    setShowForm(true);
  };

  const handleEdit = (course) => {
    setFormData({
      name: course.name,
      code: course.code || '',
      teacherId: course.teacherId || course.teacher?.id || '',
      description: course.description || '',
      semester: course.semester || 1,
      status: course.status
    });
    setEditingCourse(course);
    setFormErrors({});
    setShowForm(true);
  };

  const validateForm = () => {
    const errors = {};

    const trimmedName = formData.name?.trim() || '';
    if (!trimmedName) {
      errors.name = '  ';
    } else if (trimmedName.length < 3) {
      errors.name = '     3 ';
    } else if (trimmedName.length > 100) {
      errors.name = '     100 ';
    }

    const trimmedCode = formData.code?.trim() || '';
    if (!trimmedCode) {
      errors.code = '  ';
    } else if (trimmedCode.length < 2) {
      errors.code = '     2 ';
    } else if (trimmedCode.length > 20) {
      errors.code = '     20 ';
    } else if (!/^[-A-Z\-\d]+$/i.test(trimmedCode)) {
      errors.code = '     ,    (, -101)';
    }

    if (!formData.teacherId) {
      errors.teacherId = ' ';
    }

    if (formData.semester) {
      const semesterNum = Number(formData.semester);
      if (isNaN(semesterNum) || semesterNum < 1 || semesterNum > 8) {
        errors.semester = '     1  8';
      }
    }

    setFormErrors(errors);
    return Object.keys(errors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    const trimmedFormData = {
      ...formData,
      name: formData.name?.trim() || '',
      code: formData.code?.trim() || ''
    };
    
    setFormData(trimmedFormData);
    
    if (!validateForm()) return;

    try {
      if (editingCourse) {
        await onUpdateCourse(editingCourse.id, trimmedFormData);
      } else {
        await onCreateCourse(trimmedFormData);
      }
      setShowForm(false);
      setEditingCourse(null);
    } catch (error) {
    }
  };

  const handleToggleStatus = async (course) => {
    const newStatus = course.status === 'active' ? 'inactive' : 'active';
    await onUpdateCourse(course.id, { status: newStatus });
  };

  const handleDelete = (course) => {
    setCourseToDelete(course);
    setShowDeleteConfirm(true);
  };

  const confirmDelete = async () => {
    if (courseToDelete) {
      try {
        await onDeleteCourse(courseToDelete.id);
        setShowDeleteConfirm(false);
        setCourseToDelete(null);
      } catch (error) {
      }
    }
  };

  const columns = [
    {
      key: 'name',
      title: ' ',
      width: '20%',
      render: (value, course) => (
        <div className="course-name-cell">
          <div className="course-name">{value}</div>
          {course.code && (
            <div className="course-code">{course.code}</div>
          )}
        </div>
      )
    },
    {
      key: 'teacher',
      title: '',
      width: '20%',
      render: (value, course) => (
        <div className="teacher-cell">
          <div className="teacher-name">{course.teacher?.name || value}</div>
        </div>
      )
    },
    {
      key: 'studentsCount',
      title: '',
      width: '15%',
      render: (value) => (
        <div className="count-cell">
          <span className="count-value">{value || 0}</span>
        </div>
      )
    },
    {
      key: 'semester',
      title: '',
      width: '10%',
      render: (value) => (
        <div className="count-cell">
          <span className="count-value">{value || 1}</span>
        </div>
      )
    },
    {
      key: 'status',
      title: '',
      width: '8%',
      render: (value) => (
        <Badge
          variant={value === 'active' ? 'success' : 'secondary'}
        >
          {value === 'active' ? '' : ''}
        </Badge>
      )
    },
    {
      key: 'actions',
      title: '',
      width: '18%',
      render: (value, course) => (
        <div className="course-actions">
          <Button 
            size="small" 
            variant="outline"
            onClick={() => handleEdit(course)}
            icon=""
          />
          <Button 
            size="small" 
            variant={course.status === 'active' ? 'warning' : 'success'}
            onClick={() => handleToggleStatus(course)}
            icon={course.status === 'active' ? '' : ''}
          />
          <Button 
            size="small" 
            variant="danger"
            onClick={() => handleDelete(course)}
            icon=""
          />
        </div>
      )
    }
  ];

  return (
    <div className={`course-management ${className}`}>
      <div className="course-management__header">
        <div className="header-info">
          <h1> </h1>
          <p>     </p>
        </div>
        <Button 
          variant="primary" 
          onClick={handleCreate}
          icon=""
        >
           
        </Button>
      </div>

      {showForm && (
        <Card className="course-form-card">
          <h3>{editingCourse ? ' ' : ' '}</h3>
          <form onSubmit={handleSubmit}>
            <div className="form-grid">
              <div className="form-group">
                <label>  *</label>
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) => setFormData(prev => ({ ...prev, name: e.target.value }))}
                  className={formErrors.name ? 'error' : ''}
                  placeholder="  "
                />
                {formErrors.name && <div className="error-text">{formErrors.name}</div>}
              </div>
              
              <div className="form-group">
                <label>  *</label>
                <input
                  type="text"
                  value={formData.code}
                  onChange={(e) => setFormData(prev => ({ ...prev, code: e.target.value }))}
                  className={formErrors.code ? 'error' : ''}
                  placeholder=": CS101"
                />
                {formErrors.code && <div className="error-text">{formErrors.code}</div>}
              </div>
              
              <div className="form-group">
                <label> *</label>
                <select
                  value={formData.teacherId}
                  onChange={(e) => setFormData(prev => ({ ...prev, teacherId: e.target.value }))}
                  className={formErrors.teacherId ? 'error' : ''}
                >
                  <option value=""> </option>
                  {teachers.map(teacher => (
                    <option key={teacher.id} value={teacher.id}>
                      {teacher.name}
                    </option>
                  ))}
                </select>
                {formErrors.teacherId && <div className="error-text">{formErrors.teacherId}</div>}
              </div>

              <div className="form-group">
                <label></label>
                <input
                  type="number"
                  value={formData.semester}
                  onChange={(e) => setFormData(prev => ({ ...prev, semester: parseInt(e.target.value) || 1 }))}
                  min="1"
                  max="8"
                />
              </div>
            </div>

            <div className="form-group">
              <label> </label>
              <textarea
                value={formData.description}
                onChange={(e) => setFormData(prev => ({ ...prev, description: e.target.value }))}
                rows="3"
                placeholder="    ..."
              />
            </div>

            <div className="form-actions">
              <Button type="submit" variant="primary">
                {editingCourse ? ' ' : ' '}
              </Button>
              <Button 
                type="button" 
                variant="outline"
                onClick={() => setShowForm(false)}
              >
                
              </Button>
            </div>
          </form>
        </Card>
      )}

      <Card className="courses-table-container">
        <Table
          columns={columns}
          data={courses}
          emptyState={{
            icon: '',
            title: '  ',
            description: '     '
          }}
          striped
          hoverable
        />
      </Card>

      <ConfirmModal
        isOpen={showDeleteConfirm}
        onClose={() => {
          setShowDeleteConfirm(false);
          setCourseToDelete(null);
        }}
        onConfirm={confirmDelete}
        title=" "
        message={courseToDelete ? ` ,     "${courseToDelete.name}"?` : ''}
        confirmText=""
        cancelText=""
        danger={true}
      />
    </div>
  );
};

export default CourseManagement;