export const getAssignmentStatusInfo = (assignmentOrStatus) => {
  const status = typeof assignmentOrStatus === 'string' 
    ? assignmentOrStatus 
    : assignmentOrStatus?.status;
  
  const statusMap = {
    'not_submitted': { label: ' ', variant: 'danger', icon: '' },
    'submitted': { label: ' ', variant: 'warning', icon: '' },
    'graded': { label: '', variant: 'success', icon: '' },
    'returned': { label: '', variant: 'danger', icon: '' },
    'active': { label: '', variant: 'success', icon: '' },
    'inactive': { label: '', variant: 'danger', icon: '' }
  };
  
  return statusMap[status] || statusMap['not_submitted'];
};

export const getPriorityInfo = (priority) => {
  const priorityMap = {
    'high': { label: '', color: '#dc3545', icon: '' },
    'medium': { label: '', color: '#ffc107', icon: '' },
    'low': { label: '', color: '#28a745', icon: '' }
  };
  
  return priorityMap[priority] || priorityMap['medium'];
};

export const getDaysUntilDeadlineWithColor = (deadline) => {
  const days = getDaysUntilDeadline(deadline);
  
  if (days === null) return { days: '', color: '#6c757d' };
  if (days < 0) return { days: '', color: '#dc3545' };
  if (days === 0) return { days: '', color: '#fd7e14' };
  if (days <= 3) return { days: `${days} `, color: '#ffc107' };
  
  return { days: `${days} `, color: '#28a745' };
};

export const getDaysUntilDeadline = (deadline) => {
  if (!deadline) return null;
  
  try {
    const today = new Date();
    const deadlineDate = new Date(deadline);
    
    if (isNaN(deadlineDate.getTime())) return null;
    
    const diffTime = deadlineDate - today;
    return Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  } catch (error) {
    return null;
  }
};

