import React from 'react';
import Button from '../../UI/Button/Button';
import { useNotification } from '../../../context/NotificationContext';
import { formatDate } from '../../../utils';
import './SubmissionModal.scss';

const SubmissionModal = ({ 
  assignment, 
  isOpen, 
  onClose, 
  submissionFile, 
  onFileSelect, 
  onSubmit 
}) => {
  const { showWarning, showError } = useNotification();
  
  if (!isOpen || !assignment) return null;

  const handleSubmit = () => {
    if (assignment.submissionType === 'file') {
      if (!submissionFile) {
        showWarning(',    ');
        return;
      }
      
      const maxFileSize = (assignment.maxFileSize || 50) * 1024 * 1024;
      if (submissionFile.size > maxFileSize) {
        showError(`  .  : ${assignment.maxFileSize || 50} `);
        return;
      }
      
      if (submissionFile.size === 0) {
        showError('    ');
        return;
      }
      
      const allowedFormats = assignment.allowedFormats || ['.pdf', '.docx', '.zip'];
      const fileExtension = '.' + submissionFile.name.split('.').pop()?.toLowerCase();
      if (!allowedFormats.includes(fileExtension)) {
        showError(`  . : ${allowedFormats.join(', ')}`);
        return;
      }
      
      const invalidChars = /[<>:"/\\|?*]/;
      if (invalidChars.test(submissionFile.name)) {
        showError('    ');
        return;
      }
    }
    
    const deadline = new Date(assignment.deadline);
    const now = new Date();
    if (deadline < now) {
      showWarning('   .     .');
    }
    
    onSubmit();
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <div className="modal-header">
          <h3> : {assignment.title}</h3>
          <button className="modal-close" onClick={onClose}></button>
        </div>
        
        <div className="modal-body">
          <SubmissionInfo assignment={assignment} />
          
          {assignment.submissionType === 'file' ? (
            <FileUpload 
              assignment={assignment}
              submissionFile={submissionFile}
              onFileSelect={onFileSelect}
            />
          ) : (
            <DemoSubmission />
          )}
        </div>
        
        <div className="modal-actions">
          <Button variant="secondary" onClick={onClose}>
            
          </Button>
          <Button 
            variant="primary"
            onClick={handleSubmit}
            disabled={assignment.submissionType === 'file' && !submissionFile}
          >
              
          </Button>
        </div>
      </div>
    </div>
  );
};

const SubmissionInfo = ({ assignment }) => {
  const allowedFormats = assignment.allowedFormats || ['.pdf', '.docx', '.zip'];
  const maxFileSize = assignment.maxFileSize || 50;
  
  return (
    <div className="submission-info">
      <p><strong>:</strong> {assignment.course}</p>
      <p><strong>:</strong> {assignment.teacher}</p>
      <p><strong> :</strong> {assignment.submissionType === 'file' ? '' : ''}</p>
      <p><strong> :</strong> {formatDate(assignment.deadline)}</p>
      
      {assignment.submissionType === 'file' && (
        <>
          <p><strong> :</strong> {allowedFormats.join(', ')}</p>
          <p><strong> :</strong> {maxFileSize} </p>
        </>
      )}
    </div>
  );
};

const FileUpload = ({ assignment, submissionFile, onFileSelect }) => {
  const { showError } = useNotification();
  const allowedFormats = assignment.allowedFormats || ['.pdf', '.docx', '.zip'];
  const maxFileSize = (assignment.maxFileSize || 50) * 1024 * 1024;
  
  const handleFileChange = (e) => {
    const file = e.target.files?.[0];
    if (!file) return;
    
    if (file.size > maxFileSize) {
      showError(`  .  : ${assignment.maxFileSize || 50} `);
      e.target.value = '';
      return;
    }
    
    if (file.size === 0) {
      showError('    ');
      e.target.value = '';
      return;
    }
    
    const fileExtension = '.' + file.name.split('.').pop()?.toLowerCase();
    if (!allowedFormats.includes(fileExtension)) {
      showError(`  . : ${allowedFormats.join(', ')}`);
      e.target.value = '';
      return;
    }
    
    onFileSelect(file);
  };
  
  return (
    <div className="file-upload">
      <label className="file-input-label">
        <input
          type="file"
          onChange={handleFileChange}
          accept={allowedFormats.join(',')}
        />
        <span className="file-input-button">  </span>
      </label>
      {submissionFile && (
        <div className="file-info">
          <span> {submissionFile.name}</span>
          <span> {(submissionFile.size / 1024 / 1024).toFixed(2)} </span>
        </div>
      )}
    </div>
  );
};

const DemoSubmission = () => (
  <div className="demo-submission">
    <p>     .</p>
    <p>  ,      .</p>
  </div>
);

export default SubmissionModal;